.navbar {
    margin-top: 0px;
    background: #5133a4;
    min-height: 70px;
    position: -webkit-sticky;
    position: sticky;
    width: 100%;
    box-shadow: 0 14px 28px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.22);
    color: white;
    font-size: calc(10px + 14 * (100vw - 320px)/ 800);
    z-index: 99;
    white-space: nowrap;
    display: block;
    box-sizing: border-box;
}

.navbar li {
    display: inline-block;
    padding: 26px 0;
    width: 33.3%;
    text-align: center;
}
.jumbotron {
    background-color: rgb(211,4,24);
    color: White;
    text-align: center;
    /* text-shadow: -1px 0 white, 0 1px white, 1px 0 white, 0 -1px white; */
    margin-bottom: 0px;
    /* font-family: 'Anton', sans-serif; */
    font-family: 'Bowlby One SC', cursive;
    margin-top: 300px;
}

.jumbotronContainer {
    background-color: rgb(211,4,24);
    border: white 5px solid;
    width: 60%;
    padding: 40px 0px 30px 0px;
    font-size: 75px;
    margin-left: 20%;
    /* margin-top: 40px; */
}

.display-4 {
    font-size: 100px !important; 
}
.wrapper {
    padding-top: 50px;
    background: black;
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    padding: 20px;
    justify-content: space-around;
    align-content: flex-start;
    overflow: auto;
  }
  
.card {
  background: #fff;
  height: 200px;
  margin: 1rem;
  position: relative;
  width: 225px;
  border-radius: 0px;
  /* box-shadow: 0 3px 6px #999, 0 3px 6px #999; */
  text-align: left;
}

.card>.img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background: black;
}

.card>.img-container>img {
  width: 100%
}

.card:hover {
  height: 205px;
  width: 230px;
  border: 5px white solid;
}
